import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function Password() {

    const [password, setPassword] = useState('');
    const [authenticated, setAuthenticated] = useState(false);
    
    const correctPassword = 'SLOTDebrief'; // Replace with your desired passphrase

    const handlePasswordChange = (event) => {
        setPassword(event.target.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        if (password === correctPassword) {
        setAuthenticated(true);
        } else {
        alert("Incorrect passphrase. Try again.");
        }
    };
    
    return authenticated ? (
        <div>
            <h2>Choose Role:</h2>
            <Link to="/user">User</Link> | <Link to="/host">Host</Link>
        </div>
    ) : (
        <form onSubmit={handleSubmit}>
          <input type="password" value={password} onChange={handlePasswordChange} placeholder="Enter passphrase" />
          <button type="submit">Submit</button>
        </form>
    );
}

export default Password;