import React, { useState, useEffect } from 'react';
import { auth, db } from "../firebase";
import { ref, push } from "firebase/database";

function User() {
    const [name, setName] = useState('');
    const [queue, setQueue] = useState('');

    const handleNameChange = (event) => setName(event.target.value);
    const handleQueueJoin = (queueName) => {
    // Send the name and queue to the backend (e.g., via Firebase or Socket.IO)
        console.log(`${name} joined ${queueName}`);
        const queueRef = ref(db, `queues/${queueName}/users`);
        push(queueRef, { name });
    };

    return (
        <div>
        <h2>Enter Your Name</h2>
        <input type="text" value={name} onChange={handleNameChange} placeholder="Enter your name" />
        <button onClick={() => handleQueueJoin('Queue 1')}>Join Queue 1</button>
        <button onClick={() => handleQueueJoin('Queue 2')}>Join Queue 2</button>
        <button onClick={() => handleQueueJoin('Queue 3')}>Join Queue 3</button>
        {queue && <p>You joined {queue}</p>}
        </div>
    );
}

export default User;